@use "base/variables" as *;
@use "base/mixins" as *;

.contenedor-vendedor {
  display: grid;
  gap: 2rem;
  max-width: 70rem;
  width: 90%;
  margin: 0 auto;
  margin-top: 4rem;
  grid-template-columns: repeat(2, minmax(0, auto));
  justify-content: center;
  @include Pantallas($telefono) {
    grid-template-columns: none;
    margin: 0 auto;
    margin-top: 4rem;
  }

  .card {
    text-align: center;
    padding-bottom: 1rem;
    max-width: 32rem;
    border-radius: 0;
    margin-bottom: 1rem;
    box-shadow: 0px 0px 3px 1px rgba(0, 0, 0, 0.2);
    border: 1px solid $gris;
    border-radius: 1rem;
    &:nth-last-child(3) {
      margin-bottom: 1rem;
    }
    p {
      margin: 0;
    }
    h2 {
      margin: 0 auto;
      padding: 1rem 0 2rem 0;
      max-width: 23rem;
      line-height: 2.1rem;
      text-align: center;
    }
    span {
      margin-right: 0.5rem;
      font-weight: $bold;
    }

    h2,
    span {
      color: $azulmedio;
    }

    .m-4 {
      margin-bottom: -1.5rem;
      margin-top: -1rem;
    }
    .card-informacion {
      padding: 0 2rem;
      margin-top: 7rem;
    }

    .card-imagen {
      background-image: url(../img/idea1.jpg);
      width: 28rem;
      height: 14rem;
      background-size: 100% 100%;
      background-repeat: no-repeat;

      img {
        position: absolute;
        margin-top: 6rem;
        margin-left: 6.5rem;
        border-radius: 50%;
        width: 14rem;
        height: 14rem;
        object-fit: cover;
        z-index: 0;
      }
    }

    .acciones {
      display: flex;
      justify-content: center;
      gap: 1rem;
      text-align: center;
      align-items: center;
      align-content: center;
      margin: 1rem 0;
      form {
        width: 3rem;
        height: 3rem;
      }
    }

    a {
      background-color: $verde;
      padding: 0.5rem;
      border-radius: 0.5rem;
      width: 3rem;
      height: 3rem;
    }

    &:hover {
      transform: none;
      transition: all 0.5s;
    }
  }
}

.formulario-crear__vendedores {
  margin: 0 auto;
  max-width: 60rem;
  width: 90%;
  position: relative;

  .flex__perfil {
    justify-content: start;
    place-items: center;
    margin: 1rem 0 2rem 0;
    text-align: start;

    label {
      height: 4rem;
      width: 20rem;
      left: 0;
      background-color: $azulmedio;
      color: $blanco;
      border-radius: 1rem;
      justify-content: center;
    }
    @include Pantallas($telefono){
      flex-direction: column;
    }
  }

  .fotoperfil {
    max-width: 15rem;
    width: 15rem;
    height: 15rem;
    border-radius: 50%;
    object-fit: cover;
  }

  .upload {
    background-image: url(../img/upload.svg);
    background-repeat: no-repeat;
    width: 3rem;
    height: 3rem;
    display: inline-block;
    filter: invert(100%);
    margin: -0.8rem 0;
  }

  .flex {
    div {
      flex-basis: calc(50% - 1rem);
    }
  }

  .tipodocumento {
    padding: 0.5rem;
  }
}

.flex__content {
  display: flex;
  justify-content: space-between !important;
  margin: 3.5rem 1rem 1rem 1rem;
  gap: 2rem;

  .btn-azul {
    padding: .5rem 6rem;
    margin: 0 0 1rem;
    &:hover{
      border: 1px solid $negro;
      color: $negro;
    }
  }
  @include Pantallas($telefono){
    display: flex;
    flex-direction: column;
    gap: 0;
    a {
      order: 2;
    }
    input {
      order: 1;
    }
  }
}

#particles-js {
  background: transparent;
  height: 100vh;
  margin: 0 auto;
  opacity: 80%;
  padding-top: 7rem;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: -1;
}
