@use "variables" as *;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  font-family: $fuenteprincipal;
  font-size: 1.8rem;
  line-height: 1.8;
  &.fijar-body {
    overflow: hidden;
    .header {
      z-index: -1;
    }
  }
}
p {
  color: $negro;
  font-size: 1.8rem;
}
.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.contenedor2 {
  width: 95%;
  max-width: 60rem;
  margin: 6rem auto;
}

a {
  color: $blanco;
  text-decoration: none;
  font-size: 1.9rem;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: $titulos;
  margin: $separacion2 0;
  font-weight: $bold;
  text-align: center;
}

h1 {
  font-size: 2.6rem;
}
h2 {
  font-size: 2.4rem;
}
h3 {
  font-size: 2.2rem;
  line-height: 1.8rem;
}
h4 {
  font-size: 2rem;
}

ul {
  list-style: none;
}

button{
  cursor: pointer;
}