@use "base/variables" as *;
@use "base/mixins" as *;

.comentarios {
  padding: 0;
  padding-left: 1rem;
}

.comentario {
  background-color: $azulmedio;
  line-height: 2rem;
  padding-top: 2rem;
  color: $blanco;
  border-radius: 1rem;
  blockquote {
    padding-top: 3rem;
    position: relative;

    &::before {
      content: "";
      background-image: url(../img/comilla.svg);
      background-repeat: no-repeat;
      width: 6rem;
      height: 6rem;
      position: absolute;
      left: -4rem;
      top: -2rem;
      transform: rotate(30deg);
    }
  }

  p {
    color: $blanco;
    text-align: right;
    padding: 0 2rem 2rem 0;
    margin: 0;
    font-size: 2rem;
    font-style: italic;
    font-weight: $bold;
  }
}
