@use "base/variables" as *;

.contenedor-login {
  background-image: url(../img/header.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 100vh;
  background-position: center center;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  .contenedor-tyc-izquierda {
    margin: 0;
  }

  .login {
    background-color: rgba(41, 39, 39, 0.3);
    border-radius: 1rem;
    max-width: 60rem;
    padding: 4rem 0;
    z-index: 1;
    box-shadow: 0 5px 30px $negro;

    legend, label{
      color: $blanco;
    }
  }
}


