@use "base/variables" as *;
@use "base/mixins" as *;

.contenedor-nosotros {
  display: flex;
  flex-direction: column;
  margin-bottom: 5rem;
  @include flex(space-between);
  .nosotros-imagen {
    order: -1;
  }
  .nosotros-imagen_derecha {
    order: -1;
  }

  @include tablet {
    flex-direction: row;
    margin-bottom: 2.5rem;
    .nosotros-texto,
    .nosotros-imagen,
    .nosotros-imagen_derecha {
      flex: 1 1 2rem;
    }
    .nosotros-imagen {
      order: 1;
      margin-bottom: 1rem;
    }
    .nosotros-texto {
      padding: 2rem;
      padding-left: 0;
    }
  }

  h2 {
    margin: 0;
    line-height: 2.5rem;
    color: $azulmedio;
  }
  p {
    line-height: 2.4rem;
  }

  h2,
  p {
    text-align: center;
    @include tablet {
      text-align: start;
    }
  }
}

.nosotros-imagen,
.nosotros-imagen_derecha {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
  picture {
    max-width: 35rem;
  }
}

.nosotros-texto {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.informacion-nosotros {
  h1 {
    color: $azulmedio;
  }
}
