@use "base/variables" as *;
@use "base/mixins" as *;

.footer {
  p {
    color: $blanco;
    text-align: start;
    margin: 0;
    font-size: 1.6rem;
    line-height: 2.2rem;
    // margin-bottom: 5px;
  }

  h2 {
    text-align: start;
    color: $blanco;
    margin: 1rem 0;
  }

  .logo-footer {
    height: auto;
    margin: 1rem 0 1rem -1.2rem;
    max-width: 100%;
    width: 28rem;
  }

  .contenedor-footer {
    display: flex;
    gap: 2rem;
    flex-direction: column;
    text-align: start;
    padding: 2rem 0;
    border-bottom: 1px dotted $gris;

    @media (min-width: 768px) {
      display: grid;
      gap: 3rem;
      padding: 6rem 0 7rem 0;
      grid-template-columns: repeat(3, 1fr);

      .contenedor-imagen {
        order: 2;
      }

      .contenedor-contactos {
        order: 1;
      }

      .contenedor-info {
        order: 0;
      }
    }
  }

  .derechos {
    padding: 2.5rem 0 1.5rem 0;
    p {
      align-items: center;
      display: flex;
      justify-content: center;
      margin: 0;
      @media (max-width: 480px) {
        font-size: 1.2rem;
      }
    }
  }

  .corazon {
    animation: heart 2s ease infinite;
    -webkit-animation: heart 2s ease infinite;
    height: 1.5rem;
    margin: 0 0.5rem;
    width: 1.5rem;
    display: inline-block;
  }

  @-webkit-keyframes heart {
    0% {
      transform: scale(1);
    }
    10% {
      transform: scale(1.2);
    }
  }

  @keyframes heart {
    0% {
      transform: scale(1);
    }
    10% {
      transform: scale(1.2);
    }
  }
}

.copyright {
  padding-bottom: 0.1rem;
}

.footer__networks {
  padding-bottom: 1rem;
  h2 {
    margin-bottom: 0;
  }
  a {
    display: inline-flex;
    height: 2.8rem;
    width: 2.8rem;
    padding: 0.4rem;
    border-radius: 50%;
    transition: transform 0.3s;
    &:hover {
      background-color: $gris;
      transform: scale(1.2);
    }
  }

  div {
    margin-left: -0.5rem;
  }
}

.suscripcion {
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  align-content: center;
  margin-top: 1rem;

  fieldset {
    border: 1px solid transparent;
    padding: 0;
    flex-basis: 80%;
    input:not([type="submit"]) {
      width: 100%;
      display: block;
      background-color: #f6f6f6;
      border: 1px solid transparent;
      border-radius: 5px;
      outline: none;
      padding: .8rem .5rem;
    }
  }
  .btn-suscripcion {
    flex-basis: calc(20% - 1rem);
    margin: 0;
    border-radius: 5px;
    height: 4rem;
    background-image: url(../img/chevron-right.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-color: #f6f6f6;
    &:hover {
      background-color: transparent;
      border: 1px solid $negro !important;
      filter: invert(100%);
    }
  }
}
