@use "base/variables" as *;
@use "base/mixins" as *;

.no-webp .presentacion {
  background-image: url(../img/header.webp);
}
.webp .presentacion {
  background-image: url(../img/header.jpg);
}

.presentacion {
  background-position: center center;
  background-position: 50%;
  background-size: cover;
  height: 65rem;
  position: relative;
  @include flex(center);
  .overlay-texto {
    height: 100%;
    color: $blanco;
    flex-direction: column;
    @include flex(flex-end);

    h1 {
      margin: 3rem 0 0 0;
      line-height: 3rem;
      font-weight: $medio2;
      text-transform: uppercase;
      max-width: 60rem;
      text-align: start;
    }
    img {
      text-align: start;
      max-width: 20rem;
      margin-top: 5rem;
    }
  }
}
