@use "base/variables" as *;
@use "base/mixins" as *;

//Personalizado
.btn-azul-centro {
  display: flex;
  justify-content: center;
  button {
    @include boton($azulmedio, $blanco, $blanco, $negro, $negro);
  }
}

//Generico
.btn-blanco {
  @include boton($blanco, transparent, $negro, $blanco, $blanco);
}

//Generico
.btn-azul {
  @include boton($azulmedio, transparent, $blanco, $blanco, $blanco);
}

//Personalizado
.btn-azul2 {
  @include boton($azulmedio, transparent, $blanco, $negro, $negro);
  padding: 1rem 5rem;
  text-transform: uppercase;
  border: 1px solid transparent;
}

.boton-gris {
  @include boton($grisOscuro, transparent, $blanco, $negro, $negro);
  padding: 1rem 6rem;
  margin-top: 0;
  margin-bottom: 1rem;
  text-transform: uppercase;
}

.boton-rojo-block {
  @include boton($rojo, $rojo, $blanco, $blanco, $negro);
  padding: 0.1rem 1rem;
  border-radius: 0rem;
  width: 100%;
}
.boton-verde-block {
  @include boton(#4ca146, #4ca146, $blanco, $blanco, $negro);
  padding: 0.1rem 1rem;
  border-radius: 0rem;
}
