html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.8rem;
  line-height: 1.8;
}
body.fijar-body {
  overflow: hidden;
}
body.fijar-body .header {
  z-index: -1;
}

p {
  color: #000000;
  font-size: 1.8rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.contenedor2 {
  width: 95%;
  max-width: 60rem;
  margin: 6rem auto;
}

a {
  color: #ffffff;
  text-decoration: none;
  font-size: 1.9rem;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Open Sans", sans-serif;
  margin: 2rem 0;
  font-weight: 700;
  text-align: center;
}

h1 {
  font-size: 2.6rem;
}

h2 {
  font-size: 2.4rem;
}

h3 {
  font-size: 2.2rem;
  line-height: 1.8rem;
}

h4 {
  font-size: 2rem;
}

ul {
  list-style: none;
}

button {
  cursor: pointer;
}

.seccion {
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.contenido-centrado {
  max-width: 800px;
}

.formato {
  text-transform: uppercase;
  letter-spacing: -0.5px;
  margin: 0 0 5rem 0;
  padding-top: 3rem;
  line-height: 2.5rem;
  color: #2a389d;
}

.formato-icono {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  margin-bottom: 0;
  letter-spacing: 0.4rem;
  text-transform: uppercase;
  text-align: start;
  text-decoration: none;
}

.formatoh {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  margin-bottom: 0;
  letter-spacing: 0.1rem;
  text-align: start;
  text-decoration: none;
}

.formatop {
  line-height: 2.5rem;
  padding: 0 1rem;
}

.c-azul {
  color: #2a389d;
}

.c-azulclaro {
  color: #2a389d;
}

.bg-azul {
  background: #2a389d;
  background: linear-gradient(90deg, #2a389d 27%, #3a4285 100%, #8589b0 100%);
}

.titulo-blog {
  line-height: 2.5rem;
}

.margin-bottom {
  margin-bottom: 2rem !important;
}

.w-100 {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-bottom: 2.1rem;
  margin-left: 2rem;
}

.flex {
  display: flex;
  gap: 2rem;
}
@media (max-width: 480px) {
  .flex {
    flex-direction: column;
    gap: 0;
  }
}

.btn-azul-centro {
  display: flex;
  justify-content: center;
}
.btn-azul-centro button {
  background-color: #2a389d;
  border: 0.1rem solid transparent;
  border-radius: 2rem;
  font-size: 1.8rem;
  margin: 2.5rem 0 5rem 0;
  padding: 1rem 1.5rem;
  color: #ffffff;
}
.btn-azul-centro button:hover {
  transition: all 0.3s;
  background-color: #ffffff;
  color: #000000;
  border: 0.1rem solid #000000;
}

.btn-blanco {
  background-color: #ffffff;
  border: 0.1rem solid transparent;
  border-radius: 2rem;
  font-size: 1.8rem;
  margin: 2.5rem 0 5rem 0;
  padding: 1rem 1.5rem;
  color: #000000;
}
.btn-blanco:hover {
  transition: all 0.3s;
  background-color: transparent;
  color: #ffffff;
  border: 0.1rem solid #ffffff;
}

.btn-azul {
  background-color: #2a389d;
  border: 0.1rem solid transparent;
  border-radius: 2rem;
  font-size: 1.8rem;
  margin: 2.5rem 0 5rem 0;
  padding: 1rem 1.5rem;
  color: #ffffff;
}
.btn-azul:hover {
  transition: all 0.3s;
  background-color: transparent;
  color: #ffffff;
  border: 0.1rem solid #ffffff;
}

.btn-azul2 {
  background-color: #2a389d;
  border: 0.1rem solid transparent;
  border-radius: 2rem;
  font-size: 1.8rem;
  margin: 2.5rem 0 5rem 0;
  padding: 1rem 1.5rem;
  color: #ffffff;
  padding: 1rem 5rem;
  text-transform: uppercase;
  border: 1px solid transparent;
}
.btn-azul2:hover {
  transition: all 0.3s;
  background-color: transparent;
  color: #000000;
  border: 0.1rem solid #000000;
}

.boton-gris {
  background-color: #333333;
  border: 0.1rem solid transparent;
  border-radius: 2rem;
  font-size: 1.8rem;
  margin: 2.5rem 0 5rem 0;
  padding: 1rem 1.5rem;
  color: #ffffff;
  padding: 1rem 6rem;
  margin-top: 0;
  margin-bottom: 1rem;
  text-transform: uppercase;
}
.boton-gris:hover {
  transition: all 0.3s;
  background-color: transparent;
  color: #000000;
  border: 0.1rem solid #000000;
}

.boton-rojo-block {
  background-color: #dc4b3f;
  border: 0.1rem solid transparent;
  border-radius: 2rem;
  font-size: 1.8rem;
  margin: 2.5rem 0 5rem 0;
  padding: 1rem 1.5rem;
  color: #ffffff;
  padding: 0.1rem 1rem;
  border-radius: 0rem;
  width: 100%;
}
.boton-rojo-block:hover {
  transition: all 0.3s;
  background-color: #dc4b3f;
  color: #ffffff;
  border: 0.1rem solid #000000;
}

.boton-verde-block {
  background-color: #4ca146;
  border: 0.1rem solid transparent;
  border-radius: 2rem;
  font-size: 1.8rem;
  margin: 2.5rem 0 5rem 0;
  padding: 1rem 1.5rem;
  color: #ffffff;
  padding: 0.1rem 1rem;
  border-radius: 0rem;
}
.boton-verde-block:hover {
  transition: all 0.3s;
  background-color: #4ca146;
  color: #ffffff;
  border: 0.1rem solid #000000;
}

.dark-mode {
  background-color: #1a1a1a !important;
}
.dark-mode h2,
.dark-mode h3,
.dark-mode h4,
.dark-mode .formato,
.dark-mode .entrada-blog h4,
.dark-mode .formato-icono,
.dark-mode .resumen .descripcion-resumen .precio,
.dark-mode .resumen .descripcion-resumen h2 {
  color: #2e73c2;
}
.dark-mode p {
  color: #ffffff;
}
.dark-mode .entrada-blog {
  border-top: 1px dotted #ffffff;
}
@media (max-width: 700px) {
  .dark-mode .entrada-blog img {
    border-top: 1px solid #ffffff;
  }
}
.dark-mode .card {
  box-shadow: rgba(255, 255, 255, 0.1) 0 0.06rem 1.5rem;
}
.dark-mode .card p {
  color: #000000;
}
.dark-mode .icono img {
  border-radius: 50%;
  background-color: #ffffff;
}
.dark-mode .contacto-imagen h2 {
  color: #ffffff;
}
.dark-mode .resumen .contenido-card .iconos-caracteristicas li img {
  filter: invert(1);
}
.dark-mode table.propiedades tbody td {
  color: #ffffff;
  border-bottom: 1px solid #ffffff;
}
.dark-mode table.propiedades tbody tr {
  background-color: transparent;
}
.dark-mode .contenedor-vendedor .card {
  background-color: #2a389d;
}
.dark-mode .contenedor-vendedor .card p,
.dark-mode .contenedor-vendedor .card span,
.dark-mode .contenedor-vendedor .card h2 {
  color: #ffffff;
}
.dark-mode .header.sticky {
  background: linear-gradient(90deg, #2a389d 27%, #3a4285 100%, #8589b0 100%);
}

.contenedor-error {
  position: fixed;
  top: 0;
  right: 1%;
  z-index: 1;
  display: flex;
  flex-direction: column;
  line-height: 1.5rem;
}
.contenedor-error p {
  color: #ffffff;
  margin: 0;
  max-width: 28rem;
}

.reducido {
  max-width: 60rem;
  width: 90%;
}

.alerta {
  padding: 1.5rem 2rem;
  text-align: center;
  margin: 0.5rem 0;
  border-radius: 2rem;
  transition: all 0.3s;
}
.alerta.error {
  background-color: #da2f3e;
  color: #ffffff;
  display: block;
}
.alerta p:nth-child(2) {
  margin-top: 0.5rem;
}
.alerta.oculto {
  transition: all 0.3s;
  display: none;
  visibility: hidden;
}

.msg-alerta {
  display: flex;
  align-items: center;
  justify-content: center;
}
.msg-alerta .contenedor-mensaje {
  border-radius: 2rem;
  max-width: 60rem;
  margin: 0 auto;
  width: 90%;
  color: #ffffff;
}
.msg-alerta .contenedor-mensaje h1 {
  margin-top: -2rem;
  margin-bottom: 0;
  text-transform: uppercase;
  font-size: 1.9rem;
  font-weight: 600;
}
.msg-alerta .contenedor-mensaje h2 {
  text-align: center;
  font-weight: 500;
  font-size: 1.6rem;
  margin: 0 auto;
  line-height: 1.4rem;
  max-width: 60rem;
}
.msg-alerta .contenedor-mensaje.bg-rojo {
  background-color: #dc4b3f !important;
}
.msg-alerta .contenedor-mensaje.bg-azul__exito {
  background-color: #090979 !important;
}

@media (max-width: 768px) {
  table.propiedades {
    display: block;
    overflow-x: auto;
  }
}
table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
  margin-bottom: 1rem;
}
table.propiedades thead {
  width: 100%;
  background: #2a389d;
}
table.propiedades thead th {
  color: #ffffff;
  padding: 0.5rem 2rem;
  text-transform: uppercase;
}
table.propiedades tbody {
  width: 100%;
}
table.propiedades tbody td {
  text-align: center;
  color: #000000;
  line-height: 2.4rem;
  border-bottom: 1px solid #000000;
  font-size: 1.8rem;
}
table.propiedades tbody td:nth-child(1) {
  text-align: start;
  padding-left: 1rem;
}
table.propiedades tbody tr {
  opacity: 100;
}
table.propiedades tbody tr:hover {
  background-color: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(3px);
}
table.propiedades tbody .acciones {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
}
table.propiedades .imagen-tabla {
  width: 7rem;
  margin: 0 auto;
  border-radius: 0.4rem;
}
table.propiedades .imagen-accion {
  max-width: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
table.propiedades .accion-verde {
  margin-left: 2rem;
}
table.propiedades a {
  background-color: #4db175;
  padding: 0.5rem;
  border-radius: 0.5rem;
  margin: 2.1rem 0;
}

.imagen-small {
  width: 15rem;
}

.eliminar {
  max-width: 3rem;
  background-image: url("../img/icons8-delete.svg");
  background-size: 2rem;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #dc4b3f;
  width: 3rem;
  height: 3rem;
  border-radius: 0.5rem;
  cursor: pointer;
  border: none;
  padding: 1rem;
}

@-webkit-keyframes hvr-buzz-out {
  10% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }
  20% {
    -webkit-transform: translateX(-3px) rotate(-2deg);
    transform: translateX(-3px) rotate(-2deg);
  }
  30% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }
  40% {
    -webkit-transform: translateX(-3px) rotate(-2deg);
    transform: translateX(-3px) rotate(-2deg);
  }
  50% {
    -webkit-transform: translateX(2px) rotate(1deg);
    transform: translateX(2px) rotate(1deg);
  }
  60% {
    -webkit-transform: translateX(-2px) rotate(-1deg);
    transform: translateX(-2px) rotate(-1deg);
  }
  70% {
    -webkit-transform: translateX(2px) rotate(1deg);
    transform: translateX(2px) rotate(1deg);
  }
  80% {
    -webkit-transform: translateX(-2px) rotate(-1deg);
    transform: translateX(-2px) rotate(-1deg);
  }
  90% {
    -webkit-transform: translateX(1px) rotate(0);
    transform: translateX(1px) rotate(0);
  }
  100% {
    -webkit-transform: translateX(-1px) rotate(0);
    transform: translateX(-1px) rotate(0);
  }
}
@keyframes hvr-buzz-out {
  10% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }
  20% {
    -webkit-transform: translateX(-3px) rotate(-2deg);
    transform: translateX(-3px) rotate(-2deg);
  }
  30% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }
  40% {
    -webkit-transform: translateX(-3px) rotate(-2deg);
    transform: translateX(-3px) rotate(-2deg);
  }
  50% {
    -webkit-transform: translateX(2px) rotate(1deg);
    transform: translateX(2px) rotate(1deg);
  }
  60% {
    -webkit-transform: translateX(-2px) rotate(-1deg);
    transform: translateX(-2px) rotate(-1deg);
  }
  70% {
    -webkit-transform: translateX(2px) rotate(1deg);
    transform: translateX(2px) rotate(1deg);
  }
  80% {
    -webkit-transform: translateX(-2px) rotate(-1deg);
    transform: translateX(-2px) rotate(-1deg);
  }
  90% {
    -webkit-transform: translateX(1px) rotate(0);
    transform: translateX(1px) rotate(0);
  }
  100% {
    -webkit-transform: translateX(-1px) rotate(0);
    transform: translateX(-1px) rotate(0);
  }
}
.hvr-buzz-out {
  display: inline-block;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}

.hvr-buzz-out:hover,
.hvr-buzz-out:focus,
.hvr-buzz-out:active {
  -webkit-animation-name: hvr-buzz-out;
  animation-name: hvr-buzz-out;
  -webkit-animation-duration: 0.75s;
  animation-duration: 0.75s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
}

.resultado {
  font-size: 1.4rem;
}

.left {
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 1.5rem;
  margin-top: 0.8rem;
}

.animacion-plus {
  width: 5rem;
  height: 5rem;
  margin: 0;
  border-radius: 50%;
  background-color: #2a389d;
  cursor: pointer;
}
.animacion-plus a {
  cursor: pointer;
  width: 5rem;
  height: 5rem;
  font-size: 1.6rem;
  display: inline-block;
  overflow: hidden;
}
.animacion-plus a::before {
  content: "";
  background-image: url("../img/plus.svg");
  background-repeat: no-repeat;
  background-size: 4rem;
  background-position: center center;
  transition: all ease-in-out 0.3s;
  width: 5rem;
  height: 5rem;
  display: block;
}
.animacion-plus a:hover::before {
  margin-top: -5rem;
}
.animacion-plus a::after {
  content: "";
  background-image: url("../img/plus.svg");
  background-repeat: no-repeat;
  background-size: 4rem;
  background-position: center center;
  width: 5rem;
  height: 5rem;
  display: block;
