@use "base/variables"as *;
@use "base/mixins"as *;

.navegacion.sticky {
    padding: 0 5px;

    &::before {
        content: "";
        bottom: 0;
        box-shadow: 0 0.2rem 0.3rem $azulmedio;
        height: 0.1rem;
        left: 0;
        position: absolute;
        right: 0;
    }
}

.header.sticky {
    background: $negro;
}

.header.header-inicio__azul.sticky {
    position: fixed;
    width: 100%;
    z-index: 1;
    background: linear-gradient(90deg,
            rgba(42, 56, 157, 1) 27%,
            rgba(58, 66, 133, 1) 100%,
            rgba(133, 137, 176, 1) 100%);
}

.contenedor-paginaerror {
    padding: 2rem 0;
    background: #3AA3FF;
    width: 100%;
    height: 100vh;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    .mensaje-paginaerror{
        flex-basis: 40%;
        background-color: $blanco;
        padding: 1rem 2rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: flex-start;

        @include Pantallas($tablet){
            padding: 4rem 2rem 6rem 2rem;
            flex-basis: 100%;
        }
        h1{
            font-weight: $bold;
            font-size: 10rem;
            margin-bottom: 0;
            line-height:1;
        }

        h3{
            font-size: 2.5rem;
            line-height: 2.5rem;
        }

        h1, h3{
            text-align: start;
        }
        p{
            line-height: 2.3rem;
            margin: 1rem 0 4rem 0;
        }

        a {
            background-color: $negro;
            color: $blanco;
            cursor: pointer;
            border: none;
            padding: 1rem;
            width: 50%;
            text-transform: uppercase;
            border: 1px solid $negro;
            text-align: center;
            line-height: 1.4rem;
            font-size: 1.6rem;
            @include Pantallas($telefono){
                width: 100%;
            }
            &:hover{
                transition: all 0.3s;
                background-color: transparent;
                color: $negro;
            }
        }
    }

    .imagen-paginaerror{
        flex-basis: 60%;
        @include Pantallas($tablet){
            display: none;
            visibility: hidden;
        }
    }
}