@use "base/variables" as *;
@use "base/mixins" as *;

@include tablet {
  .contenedor-cards {
    @include grid(3, $separacion3);
  }
}

.contenedor-cards{
  .card {
    box-shadow: rgb(0 0 0 / 30%) 0 0.06rem 1.5rem;
    background-color: $blanco;
    border-radius: 1rem;
    margin-bottom: 2rem;
    position: relative;
    overflow: hidden;
    transition: all 0.5s;
  
    &::after {
      content: "Tu casa es nuestra prioridad, conf√≠a en los que saben.";
      font-size: 2rem;
      color: $blanco;
      white-space: pre-line;
      background: linear-gradient(
      90deg,
      rgba(42, 56, 157, 95%) 27%,
      rgba(58, 66, 133, 95%) 100%,
      rgba(133, 137, 176, 95%) 100%
    );
      position: absolute;
      inset: 0;
      display: grid;
      padding: 0 1rem;
      line-height: 2rem;
      text-align: center;
      place-items: center;
      transform: translateY(100%);
      transition: transform 0.3s;
    }
    &:hover::after {
      transform: translateY(0);
    }
  
    &:nth-last-child(3) {
      margin-bottom: 0;
    }
  }
}
.card {
  box-shadow: rgb(0 0 0 / 30%) 0 0.06rem 1.5rem;
  background-color: $blanco;
  border-radius: 1rem;
  margin-bottom: 2rem;
  position: relative;
  overflow: hidden;

  &:nth-last-child(3) {
    margin-bottom: 0;
  }

  .imagen-card {
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
  }

  .contenido-card {
    padding: 1rem;
    h3 {
      color: $azulmedio;
      margin-top: 0;
    }
    p {
      line-height: 1.9rem;
    }
    .precio {
      color: $azulmedio;
      font-weight: $medio2;
    }
  }
}

.iconos-caracteristicas {
  padding: 0;
  margin-bottom: 0;
  li {
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
    img {
      width: 2rem;
      height: 2rem;
      margin-right: 1rem;
    }
    p {
      margin: 0;
    }
  }
}
