@use "base/variables" as *;
@use "base/mixins" as *;

.webp .contacto-imagen {
  background-image: url(../img/encuentra.jpg);
}

.no-webp .contacto-imagen {
  background-image: url(../img/encuentra.avif);
}

.contacto-imagen {
  background-repeat: no-repeat;
  background-position: center center;
  background-position: 50%;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  h2,
  p {
    color: $blanco;
  }
  p {
    max-width: 60rem;
    line-height: 2.1rem;
    margin: 0 auto;
    padding-bottom: 5rem;
  }

  button {
    margin: 0 auto;
  }
}

.flex-contacto{
  display: flex;
  gap: 1rem;
  div{
    flex-basis: calc(50% - 1rem);
  }

  @include Pantallas($telefono){
    flex-direction: column;
  }
  select{
    padding: 0.7rem 1rem !important;
  }
}
