@use "base/variables"as *;
@use "base/mixins"as *;

.header {
  position: fixed;
  width: 100%;
  background: rgba(0, 0, 0, 0);
  z-index: 1;
  padding: 1rem 0;

  .logo-header {
    max-width: 100%;
    width: 24rem;
    height: auto;
    margin-left: -1.2rem;
  }

  &.header-inicio__azul {
    position: relative;
    display: block;
    background: linear-gradient(90deg,
        rgba(42, 56, 157, 1) 27%,
        rgba(58, 66, 133, 1) 100%,
        rgba(133, 137, 176, 1) 100%);

    .navegacion {
      padding: 1rem 0;

      .item {
        display: inline-block;
        margin-right: 0.5rem;

        a {
          position: relative;
          font-size: 2rem;

          &:hover {
            color: $negro;

            &::after {
              width: 100%;
            }
          }

          &::after {
            content: " ";
            position: absolute;
            display: flex;
            width: 0;
            height: 2px;
            bottom: 0;
            background: $negro;
            transition: 0.5s ease all;
          }
        }
      }
    }
  }

  .navegacion {
    transition: 0.6s;
    width: 100%;
    @include flex(space-between);
    align-content: center;
    place-items: center;

    a {
      font-size: 2rem;
      color: $blanco;
    }

    h1,
    ul {
      margin: 0;
      cursor: pointer;
    }

    .item {
      display: inline-block;
      margin-right: 0.5rem;

      a {
        position: relative;
        font-size: 2rem;

        &:hover {
          color: $naranja;

          &::after {
            width: 100%;
          }
        }

        &::after {
          content: " ";
          position: absolute;
          display: flex;
          width: 0;
          height: 2px;
          bottom: 0;
          background: $naranja;
          transition: 0.5s ease all;
        }
      }
    }

    .dark-mode-boton {
      max-width: 3rem;
      cursor: pointer;
      filter: invert(100%);

      @include tablet {
        margin-bottom: -0.7rem;
      }
    }
  }

  .menu-iconos {
    width: 4rem;
    height: 4rem;
    position: relative;
    display: none;
    padding: 0 1px;
    cursor: pointer;

    img {
      pointer-events: auto;
    }

    .menu.active {
      opacity: 0;
      display: none;

      img {
        visibility: hidden;
      }
    }

    .close.active {
      display: block;
      opacity: 1;
      visibility: visible;
    }

    .close {
      display: none;
      visibility: hidden;
      filter: invert(10);
    }
  }

  @include Pantallas($tablet) {
    .menu-iconos {
      display: flex;
      justify-content: center;
      align-content: center;
      width: 3rem;
      height: 3.5rem;
    }

    .lista {
      width: 25rem;
      height: 100%;
      @include flex(center);
      position: fixed;
      top: 0;
      margin: 0;
      right: -100%;
      background-color: $azulmedio;
      flex-direction: column;
      align-items: center;
      transition: 0.5s ease all;

      &.active {
        right: 0;
      }

      li {
        margin-bottom: 1.5rem;
      }
    }
  }
}

.filter.active {
  transition: 0.5s ease all;
  position: fixed;
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 0;
  background-color: rgba(0, 0, 0, 0.7);
  filter: saturate(2);
}

.header.fijo {
  position: relative;
  background-color: $negro;
  padding: 2rem 0;

  h1 {
    color: $blanco;
  }

  li {
    a {
      color: $blanco;

      &:hover {
        color: $negro;
      }

      &::after {
        background-color: $negro;
      }
    }
  }
}

.i {
  z-index: -1;
}

.centrado {
  form, input {
      background-color: transparent;
      border: none;
      color: $blanco;
      padding: 0;
      cursor: pointer;
  }

  form{
    display: inline-block;
    input {
      display: inline-block;
      position: relative;
      &:hover {
        color: $naranja;

        &::after {
          width: 100%;
        }
      }

      &::after {
        content: "";
        position: absolute;
        display: flex;
        width: 0;
        height: 2px;
        bottom: 0;
        background: $naranja;
        transition: 0.5s ease all;
      }
    }
  }
}