@use "base/variables" as *;
@use "base/mixins" as *;
@use "base/globales" as *;

.sobrenosotros {
  @include Pantalla(768px) {
    @include grid(3, $separacion2);
  }

  .icono {
    text-align: center;
    flex-direction: column;
    @include flex(center);
    place-items: center;
    img {
      height: 10rem;
      width: 10rem;
    }
  }
}

.informacion {
  h1 {
    color: $azulmedio;
  }
}
