@use "base/variables"as *;
@use "base/mixins"as *;

@include tablet {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: $separacion2;
  }

  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: $separacion2;
  }
}

.entrada-blog {
  border-top: 1px dotted $negro;
  padding: 1rem 0;

  h4 {
    color: $negro;
    margin: 1rem 0 0 0;
    line-height: 2rem;
  }

  p {
    margin: 0 0 2rem 0;
    line-height: 2rem;
    font-size: 1.6rem;

    &:nth-last-child(1) {
      margin-bottom: 3rem;
      font-size: 1.6rem;
      text-align: justify;
    }

    &:nth-of-type(1) {
      &::after {
        content: "";
        display: block;
        width: 16rem;
        height: 0.2rem;
        background-color: $azulmedio;
      }
    }
  }

  @include Pantallas(700px) {
    img {
      border-top: 1px solid $negro;
      padding-top: 1rem;
    }
  }
}

.blog h3,
.comentarios h3 {
  margin-bottom: 3rem;
  letter-spacing: 0.1rem;
}

.imagen-entrada {
  display: flex;
  align-items: center;
}

.imagen-entrada__blog {
  margin-top: 4rem;
  @include grid(2, 1rem);
}

.formato__star {
  text-align: start;
  margin-bottom: 1rem;
}

.redaccion {
  margin: 1rem 0 2rem 0;
  line-height: 2rem;
  font-size: 1.6rem;

  &:nth-last-child(1) {
    margin-bottom: 3rem;
    font-size: 1.6rem;
    text-align: justify;
  }

  &:nth-of-type(1) {
    &::after {
      content: "";
      display: block;
      width: 16rem;
      height: 0.2rem;
      background-color: $azulmedio;
    }
  }
}

.resumen-propiedad{
  line-height: 2rem;
  font-size: 1.6rem;
  
  p{
    margin-top: 0;
    text-align: justify;
  }
}