@use "base/variables" as *;
@use "base/mixins" as *;

.formulario-crear {
  text-align: center;
  background-color: #e4e4e4;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.4);
  margin-bottom: 2rem;
  border-radius: 0.5rem;
  padding: 0 1rem;

  picture {
    max-width: 20rem;
    margin: 0 auto;
    padding-top: 1rem;
  }

  fieldset {
    margin: 2rem 1rem;
    border: none;
    border-top: 1px solid $grisOscuro;
    padding: 0;
  }
  legend {
    text-transform: uppercase;
    background-color: #f6f6f6;
    border-radius: 1rem;
    padding: 0.5rem 1rem;
  }

  label {
    display: block;
    margin-bottom: 1rem;
    text-align: start;
  }

  legend,
  label {
    font-size: 1.7rem;
    line-height: 2rem;
    font-weight: $bold;
  }

  label {
    margin: 1rem 0 0 0;
  }

  input:not([type="submit"]),
  textarea,
  select {
    display: block;
    background-color: #f6f6f6;
    border-bottom: 1px solid transparent;
    border-top: none;
    border-left: none;
    border-right: none;
    margin: 0;
    margin-bottom: 0.8rem;
    outline: none;
    width: 100%;
    border-radius: 1rem;
    padding: 1rem;
    &::placeholder {
      color: #a0aead;
      font-weight: $regular;
    }
  }

  .subir {
    width: 20% !important;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
    color: $negro;
  }

  label[for="imagen"] {
    color: $blanco;
    background-color: $azuloscuro;
    width: 100%;
    transition: all 0.5s;
    cursor: pointer;
    margin: 2rem 0;
    margin-bottom: 0.8rem;
    outline: none;
    border-radius: 1rem;
    padding: 1rem;
    text-align: center;
    flex-basis: 40%;
    display: flex;
    justify-content: center;
  }

  .fichero {
    // flex-basis: 60%;
    max-width: 60%;
    word-break: break-word;
  }

  textarea {
    height: 8rem;
    resize: none;
  }

  .contenedor-file {
    @include flex(row);
    align-items: center;
    align-content: center;
    gap: 2rem;
    .upload {
      background-image: url(../img/upload.svg);
      background-repeat: no-repeat;
      width: 3rem;
      height: 3rem;
      display: inline-block;
      filter: invert(100%);
      margin: -0.8rem 0;
    }
  }

  .centrar {
    display: flex;
    align-content: center;
    align-items: center;
    place-items: center;
  }

  .contenedor-tyc-izquierda {
    justify-content: flex-start;
    margin: -2rem 1rem 2rem 1rem;
  }

  .contenedor-tyc-izquierda,
  .contenedor-ver {
    display: flex;
    align-items: center;
    gap: 1rem;
    padding-top: 0.8rem;
    label {
      order: 1;
      font-size: 1.3rem;
      margin: 0;
      line-height: 1.1rem;
      flex-basis: 90%;
    }
    input {
      display: inline-block;
      margin: 0;
      width: 1.5rem;
      height: 1.5rem;
      flex-basis: 5%;
      @include Pantalla($tablet) {
        flex-basis: 3%;
      }
    }
  }
}

.crear {
  margin-bottom: 4rem;
  line-height: 2rem;
}

.sin-margen {
  margin-top: 0 !important;
}

.rojo {
  color: #ef5559;
}

.ocultar2 {
  visibility: hidden;
}

.formulario-login {
  text-align: center;
  place-items: center;
  margin: 0 auto;
  position: relative;
  fieldset {
    border: none;
    margin: 0 auto;
    width: 90%;
    max-width: 45rem;
  }
  legend {
    background-color: transparent;
    font-size: 3.9rem;
    text-transform: none;
    color: #465a65;
    line-height: 3rem;
    letter-spacing: 0.3rem;
    font-weight: $regular;
    margin-bottom: 5.3rem;
  }
  .imagen-login,
  source {
    max-width: 14rem;
    margin: 0 auto;
  }

  label,
  input,
  input:not([type="submit"]) {
    font-size: 1.8rem;
  }

  label {
    margin-top: 3rem;
    margin-bottom: 0.8rem;
    color: #465a65;
  }

  picture{
    margin-bottom: 4rem;
  }

  .input__modificado {
    display: block;
    border: none;
    width: 100%;
    border-bottom: 1px solid $blanco;
    padding: 1rem;
    padding-left: 0;
    margin-bottom: 2rem;
    background-color: transparent;
    color: $blanco;
    &::placeholder{
      color: $blanco;
    }
    &:focus{
      outline: none !important;
      border-bottom: 2px solid $azulmedio;
    }
  }

  .btn-azul2{
    margin: 0;
  }

  .centrar {
    margin-bottom: 0 !important;
  }
  .btnlogin {
    font-family: $fuenteprincipal;
    margin-top: 0;
    padding-left: 2rem;
    padding-right: 2rem;
  }
  div{
    display: flex;
    gap: 2rem;
    margin: 0 auto;
    margin-top: 2rem;
    align-content: center;
    align-items: center;
    justify-content: center;
    place-items: center;
    @include Pantallas($telefono){
      flex-direction: column;
    }
  }
}

.contenedor-ver {
  display: flex;
  position: relative;
  margin: 0 !important;
  label{
    margin: 0;
    position: absolute;
    right: 0;
    margin-bottom: 2rem;
    img{
      width: 2.5rem;
      height: 2.5rem;
    }
  }
}

.btn-inicio {
  margin: 0 auto;
  padding: .5rem 5.5rem;
}
