@use "variables"as *;

@mixin Pantalla($mediaQuerySize) {
  @media (min-width: $mediaQuerySize) {
    @content;
  }
}

@mixin Pantallas($mediaQuerySize) {
  @media (max-width: $mediaQuerySize) {
    @content;
  }
}

@mixin grid($columnas, $espacio) {
  display: grid;
  grid-template-columns: repeat($columnas, 1fr);
  gap: $espacio;
}

@mixin flex($direccion) {
  display: flex;
  justify-content: $direccion;
}

@mixin telefono {
  @media (min-width: $telefono) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: $tablet) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: $desktop) {
    @content;
  }
}

@mixin boton($bgcolor, $bgcolorh, $txcolor, $txcolorh, $borde) {
  background-color: $bgcolor;
  border: 0.1rem solid transparent;
  border-radius: 2rem;
  font-size: 1.8rem;
  margin: 2.5rem 0 5rem 0;
  padding: 1rem 1.5rem;
  color: $txcolor;

  &:hover {
    transition: all 0.3s;
    background-color: $bgcolorh;
    color: $txcolorh;
    border: 0.1rem solid $borde;
  }
}

@mixin botones($borde)  {
  display: block;
  margin: 2.5rem 0 5rem 0;
  padding: 1rem 1.5rem;
  border-radius: 3rem;
  border: none;
  position: relative;
  z-index: 1;
  color: $blanco;
  background-color: $azulmedio;
  border: 1px solid transparent;

  &::before {
    content: '';
    width: 0;
    height: 100%;
    border-radius: 3rem;
    padding: 0;
    position: absolute;
    top: 0;
    left: 0;
    background-image: linear-gradient(to right, #ffffff 0%, #ffffff 100%);
    transition: .5s ease;
    z-index: -1;
  }

  &:hover {
    color: $negro;
    border: 1px solid $borde;
    &::before {
      width: 100%;
      height: 100%;
    }
  }
}