@use "base/variables" as *;
@use "base/mixins" as *;

.resumen {
  h1 {
    text-align: start;
    text-transform: capitalize;
    margin-bottom: 2rem;
  }

  .descripcion-resumen {
    text-align: start;
    h2 {
      text-align: start;
      margin-bottom: 0;
      color: $azulmedio;
    }

    p {
      margin-top: 1rem;
      line-height: 2.5rem;
    }

    .precio {
      font-size: 2rem;
      font-weight: $bold;
      color: $azulmedio;
    }
  }

  .img-redonda {
    border-radius: 0.5rem;
  }
}

.resumen-imagenes {
  @include tablet {
    display: grid;
    grid-template-columns: 0.66fr 2fr;
    column-gap: 0.5rem;
    align-items: center;
  }

  picture {
    order: 1;
  }

  .p {
    max-width: 62.5rem;
  }

  .resumen-imagen {
    display: flex;
    flex-direction: row;
    gap: 1rem;
    padding: 0.5rem 0;

    @include tablet {
      position: -1;
      flex-direction: column;
    }

    img {
      max-height: 15rem; //11.6
      max-width: 25rem;
      margin: 0 auto;
    }
  }
}
