@use "variables" as *;
@use "base/mixins" as *;

.dark-mode {
  background-color: darken($grisOscuro, 10) !important;
  h2,
  h3,
  h4,
  .formato,
  .entrada-blog h4,
  .formato-icono,
  .resumen .descripcion-resumen .precio,
  .resumen .descripcion-resumen h2 {
    color: $azul;
  }

  p {
    color: $blanco;
  }

  .entrada-blog {
    border-top: 1px dotted $blanco;
    @include Pantallas(700px) {
      img {
        border-top: 1px solid $blanco;
      }
    }
  }
  .card {
    box-shadow: rgb(255 255 255 / 10%) 0 0.06rem 1.5rem;
    p {
      color: $negro;
    }
  }

  .icono {
    img {
      border-radius: 50%;
      background-color: $blanco;
    }
  }

  .contacto-imagen {
    h2 {
      color: $blanco;
    }
  }
  .resumen {
    .contenido-card {
      .iconos-caracteristicas {
        li {
          img {
            filter: invert(1);
          }
        }
      }
    }
  }

  table.propiedades {
    tbody {
      td {
        color: $blanco;
        border-bottom: 1px solid $blanco;
      }
      tr {
        background-color: transparent;
      }
    }
  }

  .contenedor-vendedor .card {
    background-color: $azulmedio;
    p,
    span,
    h2 {
      color: $blanco;
    }
  }

  .header.sticky {
    background: linear-gradient(90deg,
            rgba(42, 56, 157, 1) 27%,
            rgba(58, 66, 133, 1) 100%,
            rgba(133, 137, 176, 1) 100%);
  }
}
