@use "variables" as *;
@use "mixins" as *;

.seccion {
  padding-top: $separacion3;
  padding-bottom: $separacion3;
}
.contenido-centrado {
  max-width: 800px;
}

.formato {
  text-transform: uppercase;
  letter-spacing: -0.5px;
  margin: 0 0 $separacion5 0;
  padding-top: $separacion3;
  line-height: 2.5rem;
  color: $azulmedio;
}

.formato-icono {
  font-family: $fuenteprincipal;
  font-weight: $medio2;
  margin-bottom: 0;
  letter-spacing: 0.4rem;
  text-transform: uppercase;
  text-align: start;
  text-decoration: none;
}

.formatoh {
  font-family: $fuenteprincipal;
  font-weight: $medio2;
  margin-bottom: 0;
  letter-spacing: 0.1rem;
  text-align: start;
  text-decoration: none;
}

.formatop {
  line-height: 2.5rem;
  padding: 0 1rem;
}

.c-azul {
  color: $azulmedio;
}

.c-azulclaro {
  color: $azulmedio;
}

.bg-azul {
  background: rgb(42, 56, 157);
  background: linear-gradient(
    90deg,
    rgba(42, 56, 157, 1) 27%,
    rgba(58, 66, 133, 1) 100%,
    rgba(133, 137, 176, 1) 100%
  );
}

.titulo-blog {
  line-height: 2.5rem;
}

.margin-bottom {
  margin-bottom: 2rem !important;
}

.w-100 {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-bottom: 2.1rem;
  margin-left: 2rem;
}

.flex {
  display: flex;
  gap: 2rem;
  @include Pantallas($telefono){
    flex-direction: column;
    gap: 0;
  }
}
