@use "base/variables" as *;
@use "base/mixins" as *;

.contenedor-formulario {
  background-color: $azulmedio;

  border-radius: 2rem;
  picture {
    max-width: 10rem;
    margin: 0 auto;
  }
  picture,
  source,
  img {
    border-radius: 50%;
  }
  h1 {
    margin-bottom: 1rem;
    font-size: 2rem;
    text-transform: none;
  }
  h2 {
    text-align: center;
    font-weight: $regular;
    font-size: 1.8rem;
    margin: 0 auto;
    line-height: 2rem;
    padding: 0 1rem;
  }

  h1,
  h2 {
    color: $blanco;
  }
}

.formulario {
  color: $blanco;
  fieldset {
    border: 1px solid #f6f6f6;
    border-radius: 2rem;
    margin: 2rem;

    .forma-contacto {
      width: 100%;
    }
  }

  legend {
    text-transform: uppercase;
  }

  label {
    display: block;
    margin-bottom: 1rem;
    text-align: start;
  }

  legend,
  label {
    line-height: 2rem;
  }

  input:not([type="submit"]),
  textarea,
  select {
    display: block;
    background-color: #f6f6f6;
    border: 1px solid transparent;
    border-radius: 1rem;
    margin: 0;
    margin-bottom: 0.8rem;
    outline: none;
    padding: 1rem;
    width: 100%;
    &::placeholder {
      color: #a0aead;
      font-weight: $regular;
    }
  }

  textarea {
    height: 10rem;
    resize: none;
  }

  .forma-contacto {
    max-width: 30rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    justify-content: center;
  }
  input[type="radio"] {
    &:checked {
      background-color: $negro;
      border: none;
    }
  }

  select option {
    color: $negro;
  }

  select-selected,
  select-disabled {
    color: $negro;
  }

  input[type="submit"] {
    margin: 0 0 3rem 0;
  }

  input[type="date"] {
    z-index: 999;
  }

  input[type="submit"] {
    margin: 0 0 3rem 0;
  }
}

.alinear-izquierda {
  width: 95%;
}

.btn-popup {
  display: flex;
  padding: 1rem 2rem 0 0;
  justify-content: flex-end;
  font-size: 2.8rem;
  .fa-times:hover {
    color: $negro;
  }
}

//-----------------------
.overlay-popup,
.msg-alerta {
  visibility: hidden;
  background-color: rgb(0, 0, 0, 0.8);
  backdrop-filter: blur(5px);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  overflow-y: auto;
}

.overlay-popup.active,
.msg-alerta.active {
  visibility: visible;
}

.formulario {
  input[type="radio"] {
    display: none;
  }

  .forma-contacto label {
    cursor: pointer;
    position: relative;
    padding: 1rem 1.5rem 1rem 5.1rem;
    border-radius: 3rem;
    transition: all 0.2s;
    margin-right: 1rem;
    text-align: center;
    &:hover {
      background: $blanco2;
      color: $negro;
    }
  }

  .forma-contacto.no-visible {
    visibility: hidden;
    display: none;
  }

  .forma-contacto label:before {
    content: "";
    display: inline-block;
    width: 1.7rem;
    height: 1.7rem;
    position: absolute;
    left: 15px;
    border-radius: 50%;
    background: none;
    border: 2px solid $blanco2;
  }

  input[type="radio"]:checked + label:before {
    display: none;
  }

  input[type="radio"]:checked + label {
    padding: 1rem 1.5rem;
    background: $blanco2;
    border-radius: 1.5rem;
    color: $negro;
  }
}

.fecha-hora.visible {
  visibility: visible;
  display: block;
  height: 100%;
  width: 100%;
}

.fecha-hora {
  visibility: hidden;
  width: 0;
  height: 0;
  display: none;
}

.formulario {
  .contenedor-tyc {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 2rem;
    label {
      order: 1;
    }
    input {
      display: inline;
      width: 10%;
    }
  }
}
