@use "base/variables" as *;
@use "base/mixins" as *;

.contenedor-error {
  position: fixed;
  top: 0;
  right: 1%;
  z-index: 1;
  display: flex;
  flex-direction: column;
  line-height: 1.5rem;
  p {
    color: $blanco;
    margin: 0;
    max-width: 28rem;
  }
}

.reducido {
  max-width: 60rem;
  width: 90%;
}

.alerta {
  padding: 1.5rem 2rem;
  text-align: center;
  margin: 0.5rem 0;
  border-radius: 2rem;
  transition: all 0.3s;

  &.error {
    background-color: #da2f3e;
    color: $blanco;
    display: block;
  }
  p:nth-child(2) {
    margin-top: 0.5rem;
  }
  &.oculto {
    transition: all 0.3s;
    display: none;
    visibility: hidden;
  }
}

.msg-alerta {
  display: flex;
  align-items: center;
  justify-content: center;
  .contenedor-mensaje {
    border-radius: 2rem;
    max-width: 60rem;
    margin: 0 auto;
    width: 90%;
    color: $blanco;
    h1 {
      margin-top: -2rem;
      margin-bottom: 0;
      text-transform: uppercase;
      font-size: 1.9rem;
      font-weight: $medio2;
    }
    h2 {
      text-align: center;
      font-weight: $medio;
      font-size: 1.6rem;
      margin: 0 auto;
      line-height: 1.4rem;
      max-width: 60rem;
    }
    &.bg-rojo {
      background-color: $rojo !important;
    }
    &.bg-azul__exito {
      background-color: $azuloscuro !important;
    }
  }
}

@include Pantallas($tablet) {
  table.propiedades {
    display: block;
    overflow-x: auto;
  }
}
table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
  margin-bottom: 1rem;
  thead {
    width: 100%;
    background: $azulmedio;
    th {
      color: $blanco;
      padding: 0.5rem 2rem;
      text-transform: uppercase;
    }
  }

  tbody {
    width: 100%;
    td {
      text-align: center;
      color: $negro;
      line-height: 2.4rem;
      border-bottom: 1px solid $negro;
      font-size: 1.8rem;
      &:nth-child(1) {
        text-align: start;
        padding-left: 1rem;
      }
    }

    tr {
      opacity: 100;
      &:hover {
        background-color: rgb(0, 0, 0, 0.1);
        backdrop-filter: blur(3px);
      }
    }
    .acciones {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: flex-end;
    }
  }

  .imagen-tabla {
    width: 7rem;
    margin: 0 auto;
    border-radius: 0.4rem;
  }
  .imagen-accion {
    max-width: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .accion-verde {
    margin-left: 2rem;
  }

  a {
    background-color: $verde;
    padding: 0.5rem;
    border-radius: 0.5rem;
    margin: 2.1rem 0;
  }
}

.imagen-small {
  width: 15rem;
}

.eliminar {
  max-width: 3rem;
  background-image: url("../img/icons8-delete.svg");
  background-size: 2rem;
  background-repeat: no-repeat;
  background-position: center;
  background-color: $rojo;
  width: 3rem;
  height: 3rem;
  border-radius: 0.5rem;
  cursor: pointer;
  border: none;
  padding: 1rem;
}

@-webkit-keyframes hvr-buzz-out {
  10% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }
  20% {
    -webkit-transform: translateX(-3px) rotate(-2deg);
    transform: translateX(-3px) rotate(-2deg);
  }
  30% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }
  40% {
    -webkit-transform: translateX(-3px) rotate(-2deg);
    transform: translateX(-3px) rotate(-2deg);
  }
  50% {
    -webkit-transform: translateX(2px) rotate(1deg);
    transform: translateX(2px) rotate(1deg);
  }
  60% {
    -webkit-transform: translateX(-2px) rotate(-1deg);
    transform: translateX(-2px) rotate(-1deg);
  }
  70% {
    -webkit-transform: translateX(2px) rotate(1deg);
    transform: translateX(2px) rotate(1deg);
  }
  80% {
    -webkit-transform: translateX(-2px) rotate(-1deg);
    transform: translateX(-2px) rotate(-1deg);
  }
  90% {
    -webkit-transform: translateX(1px) rotate(0);
    transform: translateX(1px) rotate(0);
  }
  100% {
    -webkit-transform: translateX(-1px) rotate(0);
    transform: translateX(-1px) rotate(0);
  }
}
@keyframes hvr-buzz-out {
  10% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }
  20% {
    -webkit-transform: translateX(-3px) rotate(-2deg);
    transform: translateX(-3px) rotate(-2deg);
  }
  30% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }
  40% {
    -webkit-transform: translateX(-3px) rotate(-2deg);
    transform: translateX(-3px) rotate(-2deg);
  }
  50% {
    -webkit-transform: translateX(2px) rotate(1deg);
    transform: translateX(2px) rotate(1deg);
  }
  60% {
    -webkit-transform: translateX(-2px) rotate(-1deg);
    transform: translateX(-2px) rotate(-1deg);
  }
  70% {
    -webkit-transform: translateX(2px) rotate(1deg);
    transform: translateX(2px) rotate(1deg);
  }
  80% {
    -webkit-transform: translateX(-2px) rotate(-1deg);
    transform: translateX(-2px) rotate(-1deg);
  }
  90% {
    -webkit-transform: translateX(1px) rotate(0);
    transform: translateX(1px) rotate(0);
  }
  100% {
    -webkit-transform: translateX(-1px) rotate(0);
    transform: translateX(-1px) rotate(0);
  }
}

.hvr-buzz-out {
  display: inline-block;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}
.hvr-buzz-out:hover,
.hvr-buzz-out:focus,
.hvr-buzz-out:active {
  -webkit-animation-name: hvr-buzz-out;
  animation-name: hvr-buzz-out;
  -webkit-animation-duration: 0.75s;
  animation-duration: 0.75s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
}

.resultado {
  font-size: 1.4rem;
}

.left {
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 1.5rem;
  margin-top: 0.8rem;
}

.animacion-plus {
  width: 5rem;
  height: 5rem;
  margin: 0;
  border-radius: 50%;
  background-color: $azulmedio;
  cursor: pointer;
  a {
    cursor: pointer;
    width: 5rem;
    height: 5rem;
    font-size: 1.6rem;
    display: inline-block;
    overflow: hidden;

    &::before {
      content: "";
      background-image: url("../img/plus.svg");
      background-repeat: no-repeat;
      background-size: 4rem;
      background-position: center center;
      transition: all ease-in-out 0.3s;
      width: 5rem;
      height: 5rem;
      display: block;
    }

    &:hover {
      &::before {
        margin-top: -5rem;
      }
    }

    &::after {
      content: "";
      background-image: url("../img/plus.svg");
      background-repeat: no-repeat;
      background-size: 4rem;
      background-position: center center;
      width: 5rem;
      height: 5rem;
      display: block;
    }
  }
}

.fondo {
  width: 100%;
  height: 100%;
  position: relative;
}

.tabs {
  width: 90%;
  max-width: 70rem;
  margin: 0 auto;
  margin-bottom: 1rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, minmax(0, auto));

  button {
    position: relative;
    border: 0.1rem solid $azulmedio;
    margin: 0;
    &.actual {
      background-color: $azulmedio;
      color: $blanco;
      border: 0.1rem solid transparent;
    }
    &:hover {
      background-color: $azulmedio;
    }

    @include Pantallas($tablet) {
      font-size: 1.3rem;
      font-weight: $bold;
    }
  }
}

//-
.contenedor-app {
  margin-top: -1rem;
  height: 100%;
  place-items: center;
}

.app {
  position: relative;
  padding-bottom: 2rem;
  .apartado {
    display: none;
  }
}

.mostrar-seccion {
  display: block !important;
}
